html, body {
  color: #333;
  overscroll-behavior-y: none;
  box-sizing: border-box;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1em;
  line-height: 1.5em;
  overflow: hidden;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  flex-direction: column;
  width: 100%;
  height: 100vh;
  display: flex;
}

#container {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
  flex-grow: 1;
  width: 100%;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.column {
  vertical-align: top;
  height: calc(100vh - 40px);
  margin: 20px;
  padding: 20px;
  overflow-y: auto;
}

#files.column {
  z-index: 100;
  background-color: #fff;
  flex-shrink: 0;
  width: 300px;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 2px 0 5px #0003;
}

#core-recipe.column {
  flex-grow: 1;
  margin-left: 0;
}

#burger-menu {
  cursor: pointer;
  z-index: 200;
  font-size: 30px;
  display: none;
  position: absolute;
  top: 20px;
  left: 20px;
}

@media (width >= 768px) {
  #burger-menu {
    display: none;
  }

  #files.column {
    box-shadow: none;
    display: block;
    position: relative;
  }
}

@media (width <= 768px) {
  #burger-menu {
    display: block;
  }

  #core-recipe.column {
    width: 100%;
  }

  #container {
    flex-direction: column;
  }
}
